* {
  box-sizing: border-box;
  outline: none;
  cursor: default;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
ul,
ol,
menu {
  padding: 0;
  margin: 0;
}

ul,
ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: default;
}

input,
button,
textarea {
  padding: 0;
  border: 0;
}

button {
  cursor: pointer;
}
